.menu__link:hover:after, .menu__link:focus:after {
  width: 100%;
}

.mobile-menu {
  display: block;
}

@media (min-width: 1024px) {
  .mobile-menu {
    display: none;
  }
}

.mobile-menu__list {
  height: 100vh;
  width: 100vw;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: opacity var(--common);
  background-color: #fff8f5;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.mobile-menu__list.is-open {
  opacity: 1;
  visibility: visible;
  pointer-events: initial;
  pointer-events: auto;
  z-index: 10;
  display: block;
  overflow: scroll;
}

.mobile-menu__items {
  height: 100vh;
  width: 70%;
  transition: transform var(--common);
  background-color: rgba(255, 231, 187, .682);
  padding-top: 120px;
  padding-left: 20px;
  font-size: 20px;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(-100%);
}

.mobile-menu__list.is-open .mobile-menu__items {
  transform: translateX(0%);
}

.mobile-menu__item + .mobile-menu__item {
  margin-top: 10px;
}

input[type="checkbox"] {
  opacity: .001;
}

.mobile-menu__button {
  position: absolute;
  top: 20px;
  left: 20px;
}

.mobile-menu__label {
  --weight: 10%;
  --size: 50px;
  width: var(--size);
  height: var(--size);
  cursor: pointer;
  z-index: 100;
  align-items: center;
  justify-items: center;
  display: grid;
  position: relative;
}

.mobile-menu__label circle {
  width: 100%;
  height: 100%;
  fill: none;
  stroke: #db6300;
  stroke-width: calc(var(--weight) * .6);
  stroke-dasharray: 284;
  stroke-dashoffset: 284px;
  stroke-linecap: round;
  transition: all .4s;
}

.mobile-menu__label .hamburger {
  width: 63%;
  height: 63%;
  align-items: center;
  justify-items: center;
  display: grid;
  position: absolute;
}

.mobile-menu__label .hamburger:before, .mobile-menu__label .hamburger .center, .mobile-menu__label .hamburger:after {
  --tx: 0;
  --rt: 0deg;
  content: "";
  width: 100%;
  height: var(--weight);
  transform-origin: 100%;
  transition: var(--bounce) .1s;
  background: #db6300;
  border-radius: .3em;
  position: absolute;
}

.mobile-menu__label .hamburger .center {
  transition: var(--bounce), width var(--ease);
  transition-delay: .3s;
  right: 0;
}

.mobile-menu__label .hamburger:before {
  transform: translate(var(--tx), 100%) rotate(var(--rt) );
  top: 0;
}

.mobile-menu__label .hamburger:after {
  transform: translate(var(--tx), -100%) rotate(var(--rt) );
  bottom: 0;
}

#toggle:checked ~ .mobile-menu__label circle {
  stroke-dashoffset: 0;
  transition-delay: .1s;
}

#toggle:checked ~ .mobile-menu__label .hamburger:before, #toggle:checked ~ .mobile-menu__label .hamburger:after {
  --tx: -15%;
  --rt: 45deg;
  transition: var(--bounce) .1s;
}

#toggle:checked ~ .mobile-menu__label .hamburger:before {
  --ty: 15%;
  --rt: -45deg;
}

#toggle:checked ~ .mobile-menu__label .hamburger .center {
  width: 0;
  right: calc(var(--size) * -.16);
  transition: width var(--ease), right var(--bounce);
}

