.el__close-btn {
  z-index: -1;
  width: 40px;
  height: 40px;
  opacity: 0;
  pointer-events: none;
  cursor: pointer;
  transition: all 0s .45s;
  position: absolute;
  top: 10px;
  right: 10px;
}

.el.s--active .el__close-btn {
  z-index: 5;
  opacity: 1;
  pointer-events: auto;
  transition: all 0s 1.4s;
}

.el__close-btn:before, .el__close-btn:after {
  content: "";
  width: 100%;
  height: 8px;
  opacity: 0;
  background: #fff;
  margin-top: -4px;
  transition: opacity;
  position: absolute;
  top: 50%;
  left: 0;
}

.el.s--active .el__close-btn:before, .el.s--active .el__close-btn:after {
  opacity: 1;
}

.el__close-btn:before {
  transform: rotate(45deg)translateX(100%);
}

.el.s--active .el__close-btn:before {
  transition: all .3s cubic-bezier(.72, .09, .32, 1.57) 1.4s;
  transform: rotate(45deg)translateX(0);
}

.el__close-btn:after {
  transform: rotate(-45deg)translateX(100%);
}

.el.s--active .el__close-btn:after {
  transition: all .3s cubic-bezier(.72, .09, .32, 1.57) 1.55s;
  transform: rotate(-45deg)translateX(0);
}

.el:hover .el__bg:before {
  transform: translate3d(0, 0, 0)scale(1.1);
}

.cont.s--el-active .el:nth-child(1):not(.s--active) {
  opacity: 0;
  transition: transform .95s, opacity .95s;
  transform: scale(.5)translate3d(0%, 0, 0);
}

.el:nth-child(1) .el__inner {
  transition-delay: 0s;
}

.el:nth-child(1) .el__bg {
  transform: translate3d(0%, 0, 0);
}

.el:nth-child(1) .el__bg:before {
  background-image: url("https://images.unsplash.com/photo-1542332213-9b5a5a3fad35?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=2070&q=80");
  transition-delay: 0s;
}

.el:nth-child(2) {
  transform-origin: 155.208%;
  transform: translate3d(105.208%, 0, 0);
}

