footer {
  padding: 60px 0;
  @include for-laptop-up {
    padding: 90px 0 50px;
  }
}
.contact__text {
  font-weight: normal;
  font-size: 20px;
  text-align: center;
  margin-bottom: 50px;
}
.contact__items {
  display: flex;
  justify-content: center;
}
.contact__link {
  display: flex;
  position: relative;
  width: 80px;
  height: 80px;
  border-radius: 23px;
  margin-right: 50px;
  font-size: 60px;
  text-decoration: none;
  justify-content: center;
  align-items: center;
  color: rgba(255, 255, 255, 0.7);
  z-index: 5;
  transition: 0.3s;
}

.contact__link::after {
  content: ' ';
  position: absolute;
  left: 0;
  top: 0;
  width: 80px;
  height: 80px;
  border-radius: 23px;
  border: 2px solid rgba(255, 255, 255, 0.2);
  background: rgba(255, 255, 255, 0.4);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  z-index: -1;
  transition: 0.3s;
}
.contact__link:hover {
  transform: rotate(-15deg);
}
.contact__link:hover::before {
  transform: rotate(15deg);
}

.contact__link::before {
  content: ' ';
  position: absolute;
  width: 80px;
  height: 80px;
  background: getColor('fb-icon');
  right: -5px;
  bottom: 0;
  border-radius: 23px;
  z-index: -1;
  transform-origin: 100% 100%;
  transform: rotate(10deg);
  transition: 0.3s;
}

.contact__item:nth-child(2) a::before {
  background: getColor('github-icon');
}
